<template>
  <div class="system-menu">
    <div class="button-group">
      <span class="route-name">{{$route.meta.title}}</span>
      <el-button type="warning" size="small" @click="handleAdd()" icon="el-icon-plus">添加</el-button>
    </div>
    <tree-table v-loading="tableLoading" :columns="columns" :data-source="menuDataList" :tree-handel="treeHandel" :expand-place="1">
      <template slot-scope="scope">
        <el-dropdown trigger="click" placement="bottom">
          <el-button type="text" size="mini">
            操作
            <i class="el-icon-arrow-down el-icon--right"></i>
          </el-button>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>
              <el-button size="mini" type="text" icon="el-icon-edit" @click="handleEdit(scope.row)">编辑</el-button>
            </el-dropdown-item>
            <el-dropdown-item>
              <el-button size="mini" type="text" icon="el-icon-circle-plus-outline" @click="handleAdd(scope.row)">添加下级</el-button>
            </el-dropdown-item>
            <el-dropdown-item divided>
              <el-button size="mini" type="text" class="btn-text-red" icon="el-icon-delete" @click="handleDelete(scope.row)">删除</el-button>
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </template>
    </tree-table>
    <!-- 操作弹框 -->
    <el-dialog :title="handleTitle" :visible.sync="handleVisible" width="30%" center>
      <el-form ref="sysMenuForm" :model="sysMenuForm" status-icon label-width="90px" size="small">
        <el-form-item label="上级菜单">
          <el-row>
            <el-col :span="24">
              <el-input v-model="sysMenuForm.parent_name" type="text" disabled></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="菜单类型">
          <el-row>
            <el-col :span="24">
              <el-radio-group v-model="sysMenuForm.type">
                <el-radio :label="0">目录</el-radio>
                <el-radio :label="1">菜单</el-radio>
                <el-radio :label="2">按钮</el-radio>
              </el-radio-group>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="菜单名称">
          <el-row>
            <el-col :span="24">
              <el-input v-model.trim="sysMenuForm.name" type="text"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="链接地址">
          <el-row>
            <el-col :span="24">
              <el-input v-model.trim="sysMenuForm.url" type="text"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="权限标识">
          <el-row>
            <el-col :span="24">
              <el-input v-model.trim="sysMenuForm.perms" type="text"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="排序号">
          <el-row>
            <el-col :span="24">
              <el-input v-model.trim="sysMenuForm.order_num" type="text"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="图标">
          <el-row :gutter="5">
            <el-col :span="18">
              <el-input v-model.trim="iconName" type="text" placeholder="例如：fa fa-circle-o"></el-input>
            </el-col>
            <el-col :span="2">
              <el-button size="small" type="warning" @click="handleIconSelect">选择图标</el-button>
            </el-col>
          </el-row>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" type="primary" @click="handleConfirm">确 定</el-button>
        <el-button size="small" @click="handleCancel">取 消</el-button>
      </span>
    </el-dialog>
    <!-- 删除弹窗 -->
    <el-dialog title="删除部门" :visible.sync="delVisible" width="260px" center>
      <div class="del-dialog-cnt">删除不可恢复，是否确定删除？</div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" type="danger" @click="deleteRow">确 定</el-button>
        <el-button size="small" @click="delVisible = false">取 消</el-button>
      </span>
    </el-dialog>
    <icon-select></icon-select>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'

export default {
  data() {
    return {
      iconVisible: false,
      tableLoading: true,
      delVisible: false,
      handleTitle: '',
      handleVisible: false,
      iconName: '',
      columns: [
        {
          text: '编号',
          dataIndex: 'id',
          width: 70
        },
        {
          text: '名称',
          dataIndex: 'text'
        },
        {
          text: '图标',
          dataIndex: 'icon',
          type: 'icon',
          align: 'center',
          width: 80
        },
        {
          text: '类型',
          dataIndex: 'type',
          type: 'type'
        }
      ],
      treeHandel: {
        text: '菜单操作',
        width: 160
      },
      sysMenuForm: {
        parent_id: '',
        parent_name: '',
        name: '',
        url: '',
        perms: '',
        type: '',
        order_num: '',
        icon: ''
      }
    }
  },
  computed: {
    ...mapState({
      menuDataList: state => state.authority.menuDataList
    })
  },
  mounted() {
    this.getData()
  },
  methods: {
    ...mapActions('authority', ['getMenuList', 'addSysMenu', 'editSysMenu', 'removeMenu', 'sysMenuById']),
    ...mapActions(['getNavTree']),
    getData() {
      const vm = this
      vm.getMenuList(vm)
    },
    resetForm() {
      this.sysMenuForm = {
        parent_id: 0,
        parent_name: '根目录',
        name: '',
        url: '',
        perms: '',
        type: '',
        order_num: '',
        icon: ''
      }
    },
    handleAdd(row) {
      this.resetForm()
      this.handleTitle = '添加菜单'
      this.iconName = ''
      this.handleVisible = true
      if (row) {
        this.sysMenuForm.parent_name = row.text
        this.sysMenuForm.parent_id = row.id
      }
    },
    async handleEdit(row) {
      const vm = this
      vm.handleTitle = '修改菜单'
      vm.handleVisible = true
      let parentName = ''
      if (row.hasParent) {
        vm.sysMenuForm.sysMenuId = row.parentId
        parentName = await vm.sysMenuById(vm)
      } else {
        parentName = '根目录'
      }
      vm.sysMenuForm = {
        menu_id: row.attributes.id,
        parent_id: row.parentId,
        parent_name: parentName,
        name: row.attributes.text,
        url: row.attributes.url,
        perms: row.attributes.perms,
        type: row.attributes.type,
        order_num: row.attributes.order_num,
        icon: row.attributes.icon
      }
      vm.iconName = row.attributes.icon
    },
    handleCancel() {
      this.iconName = ''
      this.handleVisible = false
    },
    handleDelete(row) {
      this.delVisible = true
      this.sysMenuForm.menu_id = row.attributes.id
    },
    handleIconSelect() {
      this.iconVisible = true
    },
    handleConfirm() {
      const vm = this
      if (vm.sysMenuForm.type === '') {
        vm.$message({
          type: 'warning',
          message: '菜单类型不为空'
        })
        return
      }
      if (vm.sysMenuForm.name === '') {
        vm.$message({
          type: 'warning',
          message: '菜单名称不为空'
        })
        return
      }

      if (vm.handleTitle === '添加菜单') {
        vm.addSysMenu(vm)
      }
      if (vm.handleTitle === '修改菜单') {
        vm.editSysMenu(vm)
      }
      this.handleCancel()
    },
    deleteRow() {
      this.removeMenu(this)
      this.delVisible = false
    }
  }
}
</script>

<style>
  .system-menu .el-table_1_column_3 [class^="fa fa-"] {
    margin-right: 0;
  }
</style>



